@import 'src/scss/colors.scss';
@import 'src/scss/layout.scss';

$name: 'BubblesSection';

.#{$name} {
  width: 100%;
  background: linear-gradient(to bottom, #2e0f47, #0e0e0e 82.4%);

  &__header {
    padding: 1rem var(--app-vertical-margins);
  }

  &__title {
    font-weight: 300;
    letter-spacing: 0.0625rem;
    color: $purple_l_20;
  }

  &__subtitle {
    font-weight: 300;
    letter-spacing: 0.0625rem;
    color: $white;
  }

  &__bar {
    height: 0.0625rem;
    background-color: $white_a_50;
  }

  &__container {
    position: relative;
    width: 1px;
    height: 45rem;
    margin: auto;

    & > {
      // TypeScript
      :nth-child(1) {
        top: 23.5rem;
        left: -7rem;
      }

      // JavaScript
      :nth-child(2) {
        top: 10.5rem;
        left: 1.5rem;
      }

      // HTML
      :nth-child(3) {
        top: 30rem;
        left: -18rem;
      }

      // CSS
      :nth-child(4) {
        top: 15.5rem;
        left: -20rem;
      }

      // Sass
      :nth-child(5) {
        top: -1rem;
        left: 8rem;
      }

      // React
      :nth-child(6) {
        top: -5rem;
        left: -18rem;
      }

      // Vue
      :nth-child(7) {
        top: 25rem;
        left: 0rem;
      }

      // Angular
      :nth-child(8) {
        top: 8.5rem;
        left: -26.5rem;
      }

      // Git
      :nth-child(9) {
        top: 16rem;
        left: -5rem;
      }

      // GraphQL
      :nth-child(10) {
        top: 27rem;
        left: -27rem;
      }

      // Jest
      :nth-child(11) {
        top: 40rem;
        left: -4.5rem;
      }

      // Jira
      :nth-child(12) {
        top: 7.5rem;
        left: 2.5rem;
      }

      // Java
      :nth-child(13) {
        top: 8.5rem;
        left: 16rem;
      }

      // Vite
      :nth-child(14) {
        top: 22.5rem;
        left: 17rem;
      }

      // Webpack
      :nth-child(15) {
        top: 0rem;
        left: -22rem;
      }

      // NPM
      :nth-child(16) {
        top: 40rem;
        left: -24rem;
      }
    }
  }
}

@media (max-width: $small-screen-end) {
  .#{$name} {
    height: 47.5rem;

    &__title {
      margin-bottom: 0.25rem;
      font-size: 1.5rem;
    }

    &__subtitle {
      font-size: 1.125rem;

      &--2 {
        margin-bottom: 1.125rem;
      }
    }

    &__bar {
      margin-bottom: 1.5rem;
      width: 18rem;
    }

    &__container {
      transform: scale(0.75) translateY(-8rem);
    }
  }
}

@media (min-width: $medium-screen-start) and (max-width: $medium-screen-end) {
  .#{$name} {
    height: 54rem;

    &__title {
      margin-bottom: 0.25rem;
      font-size: 1.5rem;
    }

    &__subtitle {
      font-size: 1.125rem;

      &--2 {
        margin-bottom: 1.25rem;
      }
    }

    &__bar {
      margin-bottom: 1.625rem;
      width: 18rem;
    }

    &__container {
      transform: scale(0.875) translateY(-4rem);
    }
  }
}

@media (min-width: $large_screen_start) {
  .#{$name} {
    height: 63rem;

    &__title {
      margin-bottom: 0.25rem;
      font-size: 1.75rem;
    }

    &__subtitle {
      font-size: 1.25rem;

      &--2 {
        margin-bottom: 1.5rem;
      }
    }

    &__bar {
      margin-bottom: 2rem;
      width: 20rem;
    }

    &__container {
      transform: scale(1) translateY(0rem);
    }
  }
}
